      <div class="tree-fl">
        <van-sidebar
          v-model="active"
          @change="onChange(items[active].id, active)"
        >
          <van-sidebar-item
            v-for="(item, index) in items"
            :key="index"
            :title="item.text"
          >
          </van-sidebar-item>
        </van-sidebar>
      </div> 
      <div class="content" height="100vh">
        <div id="page0" v-if="active === 0">
