<script>
import XiaoMi from "@/components/categoryView/XiaoMi.vue";
// import RedMi from "@/components/categoryView/RedMi.vue";
export default {
  components: {
    XiaoMi,
    // RedMi
  },
  data: function () {
    return {
      active: 1,
      items: [],
      xiaomidata: null,
      xiaomidatas: null,
    };
  },
  created() {
    this.axios.get("fenlei.json").then((res) => {
      // console.log(res.data.data[1]);
      this.xiaomidata = res.data.data[1];
      this.items = res.data.data.map((o) => {
        return { text: o.category_name, id: o.category_id };
      });
    });
  },
  methods: {
    onChange: function (id, index) {
      // console.log(index);
      this.axios
        .get("https://apis.netstart.cn/xmsc/home/category_v2", {
          params: { cat_id: id },
        })
        .then((res) => {
          // console.log(res);
          this.xiaomidatas = res.data.data[0];
          // var pageId = document.querySelector("#page" + index);
          this.active = index;
          // window.scrollTo({
          //   top: pageId.offsetTop,
          //   behavior: "smooth",
          // });
        });
    },
  },
};
</script>

